import React from "react";
import {CourseBoxItem} from "../../types";
import "./index.scss"

const CourseBox: React.FC<CourseBoxItem> = ({cover, title, brief, duration, course_num}) => {
  return (
    <div className="course_box">
      <div className="cover">
        <img src={cover}/>
      </div>
      <div className="down">
        <div className="title">{title}</div>
        <div className="brief">{brief}</div>
        <div className="other_info">
          <div className="info_line">
            <img src="assets/ic_time.svg" alt=""/>
            <span className="tips">{`${duration}分钟`}</span>
          </div>
          <div className="info_line">
            <img src="assets/ic_book.svg" alt=""/>
            <span className="tips">{`${course_num}个课程`}</span>
          </div>
        </div>
      </div>

    </div>
  )

}

export default CourseBox;